import { Select } from '@rebass/forms';
import { TransactionResponse } from '@ethersproject/abstract-provider';
import { BigNumber } from '@ethersproject/bignumber';
import { useState } from 'react';
import { useDispatch } from 'react-redux';
import { AutoColumn } from '../Column';
import { InfoCard } from '../InfoCard';
import { useTranslation } from 'react-i18next';
import { useFaucetTokens } from '../../state/faucet-tokens/hooks';
import { useActiveWeb3React } from '../../hooks';
import { useFaucetContract } from '../../hooks/useContract';
import { calculateGasMargin, WAIT_BLOCK_CONFIRMATIONS } from '../../utils';
import { addTransaction } from '../../state/transactions/actions';
import { ButtonPrimary } from '../Button';
import { AppDispatch } from '../../state';
import { FaucetTokenDetails } from '../../state/faucet-tokens/reducer';
import humanizeDuration from 'humanize-duration';
import moment from 'moment';

import './styles.css';

const Faucet = () => {
  const { t } = useTranslation();
  const { account, chainId } = useActiveWeb3React();
  const dispatch = useDispatch<AppDispatch>();

  const faucetContract = useFaucetContract();

  const [isLoading, setIsLoading] = useState<boolean>(false);
  const [showErrorMessage, setShowErrorMessage] = useState<boolean>(false);
  const [canClaimTime, setCanClaimTime] = useState(0);

  const faucetTokens = useFaucetTokens();
  const [selectedToken, setSelectedToken] = useState<FaucetTokenDetails>({
    address: '0xc5d495EEaA84942095b769342bC71125721692Da',
    symbol: 'PMA',
    name: 'PumaPay',
    decimals: 18,
    allowance: '10000000000000000000000', // 10k PMA
    waitTime: '86400',
  });

  const currencyOptions = Object.keys(faucetTokens).map((index, x) => {
    return (
      <option key={faucetTokens[index].address} value={faucetTokens[index].address}>
        {faucetTokens[index].symbol}
      </option>
    );
  });

  const handleClaimToken = e => {
    e.preventDefault();
    claimTokens().catch(e => console.error(e));
  };

  const handleWaitTime = () => {
    faucetContract
      .lastAccessTime(account)
      .then(res => {
        setCanClaimTime(Number(String(res)));
        setShowErrorMessage(true);
      })
      .catch(err => console.error(err.message));
    setTimeout(() => {
      setShowErrorMessage(false);
    }, 7000); // hide it after 7 seconds
  };

  const claimTokens = async () => {
    try {
      setIsLoading(true);
      const estimatedGas: BigNumber = await faucetContract.estimateGas.requestTokens(selectedToken.address);

      return faucetContract
        .requestTokens(selectedToken.address, {
          gasLimit: calculateGasMargin(estimatedGas),
        })
        .then(async (response: TransactionResponse) => {
          const summary = `Claim ${selectedToken.symbol} from Faucet`;
          const hash = response.hash;
          const faucetClaim = {
            token: selectedToken,
          };
          dispatch(
            addTransaction({
              hash,
              from: account,
              chainId,
              faucetClaim,
              summary,
            }),
          );
          await response.wait(WAIT_BLOCK_CONFIRMATIONS);
          setIsLoading(false);
        })
        .catch((error: Error) => {
          console.debug(`Failed to claim tokens. Reason: ${error}`);
          setIsLoading(false);
          throw error;
        });
    } catch (err) {
      setIsLoading(false);
      if (err?.data?.message?.includes('MUST_WAIT')) handleWaitTime();
      console.debug(`Failed to claim tokens. Reason: ${err.message}`);
    }
  };

  return (
    <>
      <div className="top-wrap">
        <AutoColumn gap="lg" justify="flex-start">
          <InfoCard title={t('faucetTitle')} />
        </AutoColumn>
        <AutoColumn gap="lg" justify="flex-start">
          <div className="faucet-form-wrapper">
            <label>Please select token</label>
            <Select
              id="faucet-token"
              name="faucet-token"
              value={selectedToken?.address}
              onChange={e => {
                setSelectedToken(faucetTokens[e.target.value]);
              }}
            >
              {currencyOptions}
            </Select>

            <div className="claim-amount-description">
              You will receive{' '}
              {(Number(selectedToken.allowance) / 10 ** selectedToken.decimals).toLocaleString('en-US')}{' '}
              {selectedToken.symbol}{' '}
            </div>

            <div className="claim-amount-description">
              You can get some test BNB from{' '}
              <a href="https://testnet.binance.org/faucet-smart" target="_blank">
                here.
              </a>
            </div>

            <div className="btn-wrap">
              <ButtonPrimary onClick={handleClaimToken} disabled={isLoading} id="faucet-claim-button">
                Claim
              </ButtonPrimary>
              <div
                className="error-message"
                style={{
                  visibility: `${showErrorMessage ? 'visible' : 'hidden'}`,
                }}
              >
                You can claim tokens in{' '}
                {humanizeDuration(Number(moment(canClaimTime * 1000)) - Number(moment.now()), {
                  largest: 2,
                  round: true,
                })}
              </div>
            </div>
          </div>
        </AutoColumn>
      </div>
    </>
  );
};

export default Faucet;
